import axios from 'axios'/*安装axios引擎*/
function http(url,methods,data) {
    return new Promise(function (resolve,reject) {
        axios({
            method:methods,
            url:url,
            data:data
        }).then((data) =>{
            resolve(data)
        }).catch((err) =>{
            reject(err)
        })
    })
}
export {http}