import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/home/home.vue'
import top from '../views/top/top.vue'
import heart from '../views/heart/heart.vue'
import about from '../views/about/about.vue'
Vue.use(VueRouter);
/*转化数据到vue-app上*/
const routes = [
    {
        path: '/',
        name: 'home',
        component: home
    },
  {
    path: '/home',
    name: 'post',
    component: home
  },
  {
    path: '/about',
    name: 'about',
    component: about
  },
    {
        path: '/top',
        name: 'top',
        component: top
    },
    {
        path: '/heart',
        name: 'Heart',
        component: heart
    },
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
});

export default router
